export default class StatisticsCalc {
  // 正規乱数 - ボックス＝ミュラー法
  public static generateNormalRandom(mean: number, standardDeviation: number): number {
    let normalRandom: number = Math.sqrt(-2 * Math.log(1 - Math.random()));
    if (Math.round(Math.random()) === 1) {
      normalRandom *= Math.sin(2 * Math.PI * Math.random());
    } else {
      normalRandom *= Math.cos(2 * Math.PI * Math.random());
    }
    normalRandom = (normalRandom * standardDeviation) + mean;
    return normalRandom;
  }

  // 指定された標本の大きさを持つ正規乱数たちを作成
  public static generateSamples(count: number, mean: number, standardDeviation: number): number[] {
    let samples: number[] = [];
    for (let i: number = 0; i < count; i++) {
      samples.push(this.generateNormalRandom(mean, standardDeviation));
    }
    return samples;
  }

  // 相関関係にある正規乱数を作成
  public static generateCorrelationalRandom(normalRandom: number, correlationCoefficient: number): number {
    let y: number = this.generateNormalRandom(1, 1);
    return correlationCoefficient * normalRandom + ((1 - correlationCoefficient ** 2) ** 0.5) * y;
  }

  // 指定された正規乱数たちと相関関係にある標準正規乱数たちを作成
  public static generateCorrelationalSamples(normalRandoms: number[], correlationCoefficient: number): number[] {
    let z: number[] = normalRandoms.map((value: number): number => {
      return this.generateCorrelationalRandom(value, correlationCoefficient);
    });
    return this.standardizeSamples(z);
  }

  // 標本の標準化
  public static standardizeSamples(samples: number[]): number[] {
    let standardDeviation: number = this.calcStandardDeviation(samples);
    let mean: number = this.calcMean(samples);
    return samples.map((value: number): number => {
      if (standardDeviation !== 0) {
        return (value - mean) / standardDeviation;
      } else {
        return value - mean;
      }
    });
  }

  // 標準正規乱数の非標準化
  public static unstandardizeSamples(samples: number[], mean: number, standardDeviation: number): number[] {
    return samples.map((value: number): number => {
      return (value * standardDeviation) + mean;
    });
  }

  // 共分散
  public static calcCovariance(sampleXs: number[], sampleYs: number[]): number {
    let meanX: number = this.calcMean(sampleXs);
    let meanY: number = this.calcMean(sampleYs);
    let covariance: number = 0;
    let length: number = this.calcMinimum([sampleXs.length, sampleYs.length]);
    for (let i: number = 0; i < length; i++) {
      covariance += (sampleXs[i] - meanX) * (sampleYs[i] - meanY);
    }
    covariance /= length;
    return covariance;
  }

  // 相関係数
  public static calcCorrelationCoefficient(sampleXs: number[], sampleYs: number[]): number {
    let covariance: number = this.calcCovariance(sampleXs, sampleYs);
    let standardDeviationX: number = this.calcStandardDeviation(sampleXs);
    let standardDeviationY: number = this.calcStandardDeviation(sampleYs);
    return covariance / (standardDeviationX * standardDeviationY);
  }

  // 切断正規乱数

  // 並び替える
  public static sortSamples(samples: number[]): number[] {
    return samples.sort((a: number, b: number): number => {
      let isBig: number = 0;
      if (a < b) {
        isBig = -1;
      } else if (b < a) {
        isBig = 1;
      }
      return isBig;
    });
  };

  // 平均値
  public static calcMean(samples: number[]): number {
    return this.calcSum(samples) / samples.length;
  };

  // 中央値
  public static calcMedian(samples: number[]): number {
    let sortedSamples: number[] = this.sortSamples(samples);
    let length: number = sortedSamples.length;
    let median: number;
    if (length % 2 === 0) {
      median = (sortedSamples[(length / 2) - 1] + sortedSamples[(length / 2)]) / 2;
    } else {
      median = (sortedSamples[Math.floor(length / 2)]);
    }
    return median;
  }

  // 最頻値
  public static calcMode(samples: number[]): number[] {
    let count: number = 0;
    let modes: number[] = [];
    let sortedSamples: number[] = this.sortSamples(samples);
    let pluralSamples: {
      [sample: number]: number,
    } = {};
    let previous: number;
    sortedSamples.forEach((value: number, index: number): void => {
      if (previous === value) {
        count++;
        if (1 < count) {
          pluralSamples[value] = count;
        }
      } else {
        count = 1;
      }
      previous = value;
    });
    if (0 < Object.keys(pluralSamples).length) {
      let counts: number[] = Object.values(pluralSamples);
      let maxCount: number = this.calcMaximum(counts);
      Object.keys(pluralSamples).forEach((key: string): void => {
        if (pluralSamples[Number(key)] === maxCount) {
          modes.push(Number(key));
        }
      })
    }
    return modes;
  }

  // 合計
  public static calcSum(samples: number[]): number {
    return samples.reduce((previous: number, current: number): number => {
      return previous + current;
    });
  };

  // 分散
  public static calcVariance(samples: number[]): number {
    let mean: number = this.calcMean(samples);
    return samples.reduce((previous: number, current: number): number => {
      return previous + ((current - mean) ** 2)
    }) / samples.length;
  }

  // 標準偏差
  public static calcStandardDeviation(samples: number[]): number {
    return Math.sqrt(this.calcVariance(samples));
  }

  // 最大値
  public static calcMaximum(samples: number[]): number {
    return Math.max(...samples);
  }

  // 最小値
  public static calcMinimum(samples: number[]): number {
    return Math.min(...samples);
  }

  // kシグマ区間
  public static calcKSigmaIntervals(samples: number[]): {
    _1sigmaInterval: number,
    _2sigmaInterval: number,
    _3sigmaInterval: number,
  } {
    let kSigmaIntervalsCount: {
      _1sigmaInterval: number,
      _2sigmaInterval: number,
      _3sigmaInterval: number,
    } = {
      _1sigmaInterval: 0,
      _2sigmaInterval: 0,
      _3sigmaInterval: 0,
    }
    let standardDeviation: number = this.calcStandardDeviation(samples);
    let mean: number = this.calcMean(samples);
    samples.forEach((value: number): void => {
      if (-(standardDeviation * 3) + mean <= value
        && value <= (standardDeviation * 3) + mean) {
        kSigmaIntervalsCount._3sigmaInterval++;
      }
      if (-(standardDeviation * 2) + mean <= value
        && value <= (standardDeviation * 2) + mean) {
        kSigmaIntervalsCount._2sigmaInterval++;
      }
      if (-(standardDeviation * 1) + mean <= value
        && value <= (standardDeviation * 1) + mean) {
        kSigmaIntervalsCount._1sigmaInterval++;
      }
    });
    let kSigmaIntervals: {
      _1sigmaInterval: number,
      _2sigmaInterval: number,
      _3sigmaInterval: number,
    } = {
      _1sigmaInterval: kSigmaIntervalsCount._1sigmaInterval / samples.length,
      _2sigmaInterval: kSigmaIntervalsCount._2sigmaInterval / samples.length,
      _3sigmaInterval: kSigmaIntervalsCount._3sigmaInterval / samples.length,
    }
    return kSigmaIntervals;
  }
}