














































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import CStatistics from '@/components/links/CStatistics.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '標準化',
    },
  },
})
export default class Standardization extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataText: string = '';
  private dataStandardizedText: string = '';
  private dataFormat: string = '';
  private dataKeyRegExps: {
    sample: RegExp,
  } = {
    sample: /\{ *sample\ *\}/g,
  };
  private dataSelectedFormatExampleIndex: number = 0;
  private dataFormatExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'comma',
      caption: 'カンマ区切り',
      content: '{ sample },',
    },
    {
      name: 'new_line',
      caption: '改行区切り',
      content: '{ sample }\n',
    },
    {
      name: 'semicolon',
      caption: 'セミコロン区切り',
      content: '{ sample }:',
    },
    {
      name: 'slash',
      caption: 'スペース区切り',
      content: '{ sample } ',
    },
  ];
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'standard_normal_distribution',
      caption: '標準正規乱数',
      content: StatisticsCalc.generateSamples(512, 0, 1).map((value: number): number => { return Math.round(value * 10000) / 10000; }).join(', '),
    },
    {
      name: 'test',
      caption: 'センター試験（国語）',
      content: StatisticsCalc.generateSamples(256, 111.29, 33.10).map((value: number): number => { return Math.round(value); }).join(' '),
    },
  ];

  private created(): void {
  }

  // Methods
  private listenOnSampleExampleSelect($event: Event): void {
    this.dataSelectedSampleExampleIndex = Number(<any>$event);
    this.dataText = this.dataSampleExamples[this.dataSelectedSampleExampleIndex].content;
  }
  private listenInput($event: Event): void {
    this.dataText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnFormatInput($event: Event): void {
    this.dataFormat = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnFormatExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    this.dataFormat = this.dataFormatExamples[index].content;
    this.dataSelectedFormatExampleIndex = index;
  }
  private calcStatistics(): void {
    let samples: string[] | number[] | null = this.dataText.match(/(\+|\-)?([1-9][0-9]+|[0-9])(\.[0-9]+)?/g);
    if (samples !== null) {
      let maxDigitCount: number = 0;
      samples.forEach((value: string): void => {
        let digitCount: number = value.replace(/(\.|\+|\-)/g, '').length;
        if (maxDigitCount < digitCount) {
          maxDigitCount = digitCount;
        }
      });
      samples = samples.map((value: string): number => {
        return Number(value);
      });
      samples = StatisticsCalc.standardizeSamples(samples);
      this.formatSamples(samples, maxDigitCount - 1);
    }
  }
  private formatSamples(samples: number[] | string[], digitCount: number): void {
    let text: string = '';
    for (let i: number = 0; i < samples.length; i++) {
      let sampleText: string = this.dataFormat;
      sampleText = sampleText.replace(
        this.dataKeyRegExps.sample,
        MathUtil.toFixed(Number(samples[i]), digitCount)
      );
      text += sampleText;
    }
    this.dataStandardizedText = text;
  }
}
